.Search-container {
  text-align: center;
  margin: 20px;
}

.Search-input {
  padding: 10px;
    width: 400px;
    font-size: 16px;
    border: 3px solid #64d0f1;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px !important;
    border-radius: 20px;
    outline: none;
}

.Search-input::placeholder {
  color: #aaa; /* Placeholder text color */
}

.Search-input:focus {
  border-color: #0056b3; /* Border color when focused */
}

/* Customize the search button (optional) */
.Search-button {
  background-color: #007bff; /* Blue button background */
  color: white; /* Button text color */
  border: none;
  padding: 10px 20px;
  margin-left: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.Search-button:hover {
  background-color: #0056b3; /* Button background on hover */
}